//===== Common Virtual Class==========================

//----- Text ----------------------------------------

%list-unstyled {
    margin: 0;
    padding: 0;
    list-style: none;
}

%colorless {
    border-color: transparent;
    background-color: transparent;
    background-image: none;
    box-shadow: none;
}

//----- Scrollbar -----------------------------------
%scrollbar {
    --#{$var-prefix}scrollbar-size: #{$scrollbar-size};
    --#{$var-prefix}scrollbar-thumb-bg: var(--#{$var-prefix}gray-100);

    &::-webkit-scrollbar {
        width: var(--#{$var-prefix}scrollbar-size);
        height: var(--#{$var-prefix}scrollbar-size);
        background-color: var(--#{$var-prefix}white);

        &-track {
            box-shadow: inset 0 0 var(--#{$var-prefix}scrollbar-size)
                rgba(var(--#{$var-prefix}white-rgb), 1);
            background-color: var(--#{$var-prefix}white);
        }

        &-thumb {
            background-color: var(--#{$var-prefix}scrollbar-thumb-bg);
            @include border-radius(var(--#{$var-prefix}scrollbar-size));
        }
    }
}

// Heading
.kv-margin-xxl {
    margin-bottom: 5.6rem !important;
}
.kv-margin-lg {
    margin-bottom: 2.4rem !important;
}
.kv-margin-sm {
    margin-bottom: 1.8rem !important;
}
.kv-margin-md {
    margin-bottom: 4rem !important;
}

.kv-media {
    width: 46% !important;
}
.kv-content {
    padding: 2.6rem 0;
    flex: 1;
    background: linear-gradient(
        24.91deg,
        #eff6ff 18.37%,
        #ffffff 86.66%
    ) !important;
}
.kv-h100 {
    height: 100vh !important;
}
